


import { batchUpdateState } from '@/utils'
import { getCookie, setCookie } from '@/utils/auth'
import { 
  list,
  notice,
  remove,
  setread,
  hasnew
 } from '@/api/message'

 const beehub_new_count = process.env.VUE_APP_BEEHUB_NEWCOUNT

const state = {
  new_count:getCookie(beehub_new_count)||''
}

const mutations = {
  batchUpdateState: (state, payload) => {
    batchUpdateState(state,payload)
  },
}

const actions = {
  //  已登录会员查看发给自己的消息
  list(_, payload) {
    return new Promise((resolve, reject) => {
        list(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  已登录会员查看发给自己的系统消息
  notice(_, payload) {
    return new Promise((resolve, reject) => {
        notice(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  已登录会员移除消息
  remove(_, payload) {
    return new Promise((resolve, reject) => {
        remove(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  已登录会员设置消息已读
  setread(_, payload) {
    return new Promise((resolve, reject) => {
        setread(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  已登录会员获取发给自己的未读消息数量。
  hasnew({commit}) {
    return new Promise((resolve, reject) => {
        hasnew().then((result) => {
          const { new_count} = result.data
          commit('batchUpdateState', {new_count})
          setCookie(beehub_new_count,new_count)
          resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
