import { batchUpdateState } from '@/utils'
import { 
    materialAdd, 
    materialModify,
    materialRemove,
    materialMineList, 
    materialGet,
    articleMineList,
    articleRemove,
    articleModify,
    articleAdd,
    articleGet
  } from '@/api/article'


const state = {
 
}

const mutations = {
  batchUpdateState: (state, payload) => {
    batchUpdateState(state,payload)
  }
}

const actions = {
   //  我的文章
   articleMineList({ commit }, payload) {
    return new Promise((resolve, reject) => {
      articleMineList(payload).then((result) => {
        
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
   // 删除文章
  articleRemove({ dispatch }, payload) {
    return new Promise((resolve, reject) => {
      articleRemove(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
   // 修改文章
  articleModify(_, payload) {
    return new Promise((resolve, reject) => {
      articleModify(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
   // 添加文章
   articleAdd(_, payload) {
    return new Promise((resolve, reject) => {
      articleAdd(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
   //  文章的详细信息
   articleGet({ commit }, payload) {
    return new Promise((resolve, reject) => {
      articleGet(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  我的资料
  materialMineList({ commit }, payload) {
    return new Promise((resolve, reject) => {
      materialMineList(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  添加资料
  materialAdd(_, payload) {
    return new Promise((resolve, reject) => {
      materialAdd(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  修改资料
  materialModify(_, payload) {
    return new Promise((resolve, reject) => {
      materialModify(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  删除资料
  materialRemove(_, payload) {
    return new Promise((resolve, reject) => {
      materialRemove(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  资料的详细信息
  materialGet({ dispatch }, payload) {
    return new Promise((resolve, reject) => {
      materialGet(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
