import request from '@/utils/request'

/**
 * @description: 添加资料
 * @param {type} 
 * @return: 
 */

export function materialAdd(data) {
    return request({
      url: '/subject/material/add',
      method: 'post',
      data
    })
}

/**
 * @description: 修改资料
 * @param {type} 
 * @return: 
 */

export function materialModify(data) {
    return request({
      url: '/subject/material/modify',
      method: 'post',
      data
    })
}

/**
 * @description: 删除资料
 * @param {type} 
 * @return: 
 */

export function materialRemove(data) {
    return request({
      url: '/subject/material/remove',
      method: 'post',
      data
    })
}



/**
 * @description:  话题下本人资料 sid
 * @param {type} 
 * @return: Promise
 */

export function materialMineList(data) {
  return request({
    url: '/subject/material/mine',
    method: 'get',
    params:data
  })
}

/**
 * @description: 文章的详细信息
 * @param {type} 
 * @return: Promise
 */

export function materialGet(data) {
  return request({
    url: '/subject/material/get',
    method: 'get',
    params: data
  })
}

