<template>
  <el-breadcrumb class="app-breadcrumb" separator="/">
    <transition-group name="breadcrumb">
      <el-breadcrumb-item v-for="(item,index) in breadcrumb" :key="index">
        <router-link v-if="item.path" :to="item.path">{{item.name}}</router-link>
        <a v-else>
          <!-- <span v-if="item.name == 'xx工作室'"></span>
          <span v-else></span> -->
            {{item.name}}
        </a>
      </el-breadcrumb-item>
    </transition-group>
  </el-breadcrumb>
</template>

<script>
import pathToRegexp from 'path-to-regexp'

export default {
  data() {
    return {
      breadcrumb: []
    }
  },
  watch: {
    $route() {
      this.getBreadcrumb()
    }
  },
  created() {
    this.getBreadcrumb()
  },
  methods: {
    getBreadcrumb() {
      this.breadcrumb = this.$route.meta && this.$route.meta.breadcrumb ?  this.$route.meta.breadcrumb : []
    },
   
  },
   
}
</script>


