<!--
 * @Description: 头像组件，支持size参数:mini,small,medium,name参数,
 * @Author: icony/精武陈真
 * @Date: 2019-06-21 18:22:11
 * @LastEditTime: 2019-07-19 14:28:24
 * @LastEditors: icony/精武陈真
 -->

<template>
    <div v-if="headIcon === ''" :class="computedClass" :style="{background:bgColor}">
        {{name}}
    </div>
    <div v-else :class="computedClass">
       <img :src="headIcon" width="100%" />
    </div>
</template>
<<script>
export default {
    name: 'Avatar',
     props: {
         size: {
             type:String,
             default:'medium'
         },
         name: {
             type:String,
             default:''
         },
         bgColor:{
             type: String,
             default:'#FF7765'
         },
         headIcon:{
             type:String,
             default:''
         }
     },
    
     computed:{
        computedClass: function (vm) {
             const { size } = vm;
             return {
                [size]: true,
                avatar: true,
             }
        },
     }
}
</script>
<style lang="scss" scoped>
 .avatar {
     position: relative;
     border-radius: 50%;
     text-align: center;
     color: #fff;
     display: inline-block;
     overflow: hidden;
 }

 .small {
    height: 55px;
    width:  55px;
    line-height: 55px;
    font-size:18px;
 }
 .mini {
    height: 30px;
    width:  30px;
    line-height: 30px;
    font-size:14px;
 }

 
 .medium {
    height: 70px;
    width:  70px;
    line-height: 70px;
    font-size:20px;
 }
</style>
