import { 
  login, 
  logout,
  inituser,
  verifyanswer, 
  getuser,
  changenick,
  changepassword,
  changeqa,
  adduser,
  checkAccount,
  createdbyme,
  changeheadicon
} from '@/api/user'
import { getCookie, setCookie, removeCookie } from '@/utils/auth'
import { resetRouter } from '@/router'
import { batchUpdateState,getNavigatorLang } from '@/utils'
const beehub_user = process.env.VUE_APP_BEEHUB_USER


const updateUserInfo = (commit,data)=>{
  const { token,user} = data;
  if(token){
    setCookie(token)
  }
  setCookie(process.env.VUE_APP_BEEHUB_USER,JSON.stringify(user))
  commit('batchUpdateState', data)
}
const state = {
  token: getCookie()||'',
  lang: getNavigatorLang()||'zh-cn',
  user: getCookie(beehub_user)?JSON.parse(getCookie(beehub_user)):''
 
}

const mutations = {
  batchUpdateState: (state, payload) => {
    batchUpdateState(state,payload)
  },
}

const actions = {
  // user login
  login({ commit }, userInfo) {
    return new Promise((resolve, reject) => {
      login(userInfo).then(response => {
        const { token,user } = response.data
        // 还未设置昵称信息
        if(user.inited == 0){
          commit('batchUpdateState', {token}) //只存token,user不存，这个时user中inited是变化
        }else{
          updateUserInfo(commit,{token,user})
        }
        
        resolve(user)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // user logout
  logout({ dispatch }) {
    return new Promise((resolve, reject) => {
      logout().then((result) => {
        dispatch('resetUserInfo').then(()=>{
          console.log(APP_GLOBAL_ROUTER)
          // location.href="/login"
          APP_GLOBAL_ROUTER.push('/login')
        })
        resetRouter()
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
   // 获取用户资料
   getuser({ commit },payload) {
    return new Promise((resolve, reject) => {
      getuser(payload).then((result) => {
        updateUserInfo(commit,{user: result.data})
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // remove token
  resetUserInfo({ commit }) {
    return new Promise(resolve => {
      commit('batchUpdateState', {token:'',lang:'zh-cn',user:''})
      removeCookie() // token
      removeCookie(process.env.VUE_APP_BEEHUB_USER) // beehub_user
      resolve()
    })
  },
  // 初始化自定义问题
  inituser({ commit },payload) {
    return new Promise((resolve, reject) => {
      inituser(payload).then((result) => {
        updateUserInfo(commit,result.data)
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
   // 验证自定义问题
   verifyanswer({ commit }, payload) {
    return new Promise((resolve, reject) => {
      verifyanswer(payload).then((result) => {
        updateUserInfo(commit,result.data)
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // 修改昵称
  changenick({ dispatch }, payload) {
    return new Promise((resolve, reject) => {
      changenick(payload).then((result) => {
        dispatch('getuser',{});
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  修改密码
  changepassword(_, payload) {
    return new Promise((resolve, reject) => {
      changepassword(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  修改安全问题及答案
  changeqa(_, payload) {
    return new Promise((resolve, reject) => {
      changeqa(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  添加账号
  adduser(_, payload) {
    return new Promise((resolve, reject) => {
      adduser(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  检查账户是否可注册
  checkAccount(_, payload) {
    return new Promise((resolve, reject) => {
      checkAccount(payload).then((result) => {
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
  //  本人创建的用户列表
  createdbyme(_, payload) {
      return new Promise((resolve, reject) => {
        createdbyme(payload).then((result) => {
          resolve(result)
        }).catch(error => {
          reject(error)
        })
      })
  },
  //  修改用户头像
  changeheadicon({dispatch}, payload) {
    return new Promise((resolve, reject) => {
      changeheadicon(payload).then((result) => {
        dispatch('getuser',{});
        resolve(result)
      }).catch(error => {
        reject(error)
      })
    })
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}

