<template>
  <div id="app" >
    <router-view />
    <div class="top-button" v-show="isShow">
       <el-link  @click="goTop(50)" :underline="false"><svg-icon icon-class="top" style="width:36px; height:36px" /></el-link>
    </div>
  
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      isShow: false
    }
  },
  mounted() {
    document.getElementById("app").onscroll = this.handleScroll
  },
  methods: {
    handleScroll(){
      var top = document.getElementById("app").scrollTop;     
        if(top>100){
          this.isShow = true;
        }else{
          this.isShow = false;
        }
    },
    goTop  (i) {
      this.$nextTick(() => {
        document.getElementById("app").scrollTop -= i;     
      })
      if (document.getElementById("app").scrollTop>0) {
        var timer=setTimeout(()=>this.goTop(i),30);
      }else {
        clearTimeout(timer);
      }
    },
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}

</script>
<style lang="scss" scoped>
  .top-button{
    position:fixed; bottom:7%; right:10%;
  }
</style>
