import request from '@/utils/request'

/**
 * @description: 已登录会员查看发给自己的消息
 * @param {page,limit,status} 
 * @return: Promise
 */
export function list(data) {
    return request({
      url: '/user/message/list',
      method: 'get',
      params:data
    })
}

/**
 * @description: 会员查看系统消息
 * @param {page,limit} 
 * @return: Promise
 */
export function notice(data) {
    return request({
      url: '/user/message/notice',
      method: 'get',
      params:data
    })
}  


/**
 * @description: 已登录会员移除消息
 * @param {id} 
 * @return: Promise
 */
export function remove(data) {
    return request({
      url: '/user/message/remove',
      method: 'post',
      data
    })
}

/**
 * @description: 已登录会员设置消息已读
 * @param {id} 
 * @return: Promise
 */
export function setread(data) {
    return request({
      url: '/user/message/setread',
      method: 'post',
      data
    })
}



/**
 * @description: 已登录会员获取发给自己的未读消息数量。
 * @param {id} 
 * @return: Promise
 */
export function hasnew(data) {
    return request({
      url: '/user/message/hasnew',
      method: 'get',
      params: data
    })
}